/* Main container styling */
.loading-screen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('./image/lod.jpg'); /* Background image */
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  overflow: hidden;
  font-family: 'Courier New', Courier, monospace;
  text-align: center;
}

/* Loading button styles */
.loading-button {
  padding: 1.5rem 3rem;
  font-size: 1.8rem;
  color: #000000; /* Set button text color to black */
  background-color: #ffffff; /* Set button background to white */
  border: 2px solid #2962FF;  /* Red border */
  border-radius: 10px; /* More rounded corners */
  box-shadow: 0 0 15px #2962FF; /* Soft glowing red shadow */
  animation: fadeInOut 3s ease-in-out infinite, softPulse 4s ease-in-out infinite;
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  text-shadow: 0 0 10px white; /* White glow for text */
  cursor: default;
}

/* Small blinking dot */
.loading-dot {
  width: 12px;
  height: 12px;
  margin-right: 15px;
  background-color: #ff0000;
  border-radius: 50%;
  animation: blink 1s infinite;
}

/* Soft glow background animation */
.soft-bg-animation {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, rgba(36, 0, 240, 0.041), rgba(7, 0, 44, 0.397));
  animation: softGlow 6s ease-in-out infinite;
  z-index: -1;
}

/* Blinking dot animation */
@keyframes blink {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.3;
  }
}

/* Text fade in-out animation */
@keyframes fadeInOut {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
}

/* Text scaling and glow pulsing */
@keyframes softPulse {
  0% {
    transform: scale(1);
    text-shadow: 0 0 7px #2962FF;
  }
  50% {
    transform: scale(1.05);
    text-shadow: 0 0 10px #2962FF;
  }
  100% {
    transform: scale(1);
    text-shadow: 0 0 7px #2962FF;
  }
}

/* Background soft glow animation */
@keyframes softGlow {
  0%, 100% {
    opacity: 1;
    filter: blur(0px);
  }
  50% {
    opacity: 0.8;
    filter: blur(3px);
  }
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .loading-button {
    padding: 1rem 2rem;
    font-size: 1.2rem;
    border-radius: 8px;
  }
}

@media (max-width: 480px) {
  .loading-button {
    padding: 0.8rem 1.5rem;
    font-size: 1rem;
    border-radius: 6px;
  }
}
